<template>
  <div class="overflow-hidden select-none">
    <div
      class="sticky top-0 bg-gray-800 text-gray-300 header flex items-center justify-between"
    >
      <div class="flex">
        <span class="ml-3">OBS Remote Web Client</span>
        <span class="ml-1.5" v-if="connected"> - Connected to {{ host }})</span>
      </div>
      <div class="flex">
        <div class="mr-3 focus:outline-none" v-on:click="fullscreen">
          <i class="fas fa-expand fa-2x"></i>
        </div>
        <div
          class="mr-3 focus:outline-none"
          :disabled="!connected"
          v-on:click="disconnect"
          v-if="connected"
        >
          <i class="fas fa-times fa-2x"></i>
        </div>
      </div>
    </div>

    <div class="flex flex-col bg-gray-900 text-gray-400 overflow-y-auto">
      <div
        class="bg-white bg-opacity-30 h-full w-full block fixed"
        v-if="connecting"
      ></div>
      <div v-if="!connected">
        <div class="grid grid-cols-1 justify-items-center p-5 connected">
          <div class="w-full">
            <h1 class="mt-0 font-medium text-2xl text-gray-300">Connect</h1>
            <div class="mt-3 text-sm">
              <label for="username" class="block text-gray-300">host</label>
              <input
                type="text"
                autofocus
                class="px-4 py-3 mt-3 focus:outline-none bg-gray-800 text-gray-300 placeholder-gray-300 w-full"
                v-model="host"
                placeholder="host (your ip + port)"
              />
            </div>
            <div class="mt-3 text-sm">
              <label for="password" class="block text-gray-300">password</label>
              <input
                type="password"
                class="px-4 py-3 mt-3 focus:outline-none bg-gray-800 text-gray-300 placeholder-gray-300 w-full"
                v-model="password"
                placeholder="password (your server settings password)"
              />
            </div>
            <button
              class="mt-3 mb-3 block text-center text-gray-300 bg-gray-800 p-3 duration-300 hover:bg-gray-600 focus:outline-none w-full"
              :disabled="connecting"
              v-on:click="connect"
            >
              Connect
            </button>
            <div v-if="httpversion | error">
              <h1 class="mt-0 font-medium text-2xl text-gray-300">
                Issue found
              </h1>
            </div>
            <div
              class="mt-3 mb-3 bg-yellow-500 text-yellow-900 p-5"
              v-if="httpsverify"
            >
              <i class="fas fa-exclamation-triangle"></i>
              This page is using an SSL certificate (secure connection), but the
              websocket will use WS (connection without certificate) it is
              recommended to use an unsecured connection (http). Enter the
              <a :href="httpversion" class="text-gray-300"
                >http version of this page</a
              >.
            </div>
            <div class="mt-3 mb-3 bg-red-400 text-red-800 p-5" v-if="error">
              <i class="fas fa-times"></i>
              {{ errormessage }}
            </div>
          </div>
        </div>
      </div>
      <div v-else>
        <div class="grid grid-cols-3 connected">
          <div class="grid grid-cols-1">
            <button
              v-for="scene in scenes"
              :key="scene.name"
              :class="[
                scene.name == currentscene
                  ? 'bg-gray-600 focus:bg-gray-600 duration-300 focus:outline-none text-gray-100 text-center text-xl'
                  : 'bg-gray-900 focus:bg-gray-900 duration-300 hover:bg-gray-800 focus:outline-none text-gray-300 text-center text-lg',
              ]"
              v-on:click="setscene(scene.name)"
            >
              {{ scene.name }}
            </button>
          </div>

          <div class="grid grid-cols-1">
            <div class="grid grid-cols-1">
              <button
                :class="[
                  streamingstate == 'Streaming'
                    ? 'bg-gray-600 focus:bg-gray-600 duration-300 focus:outline-none text-gray-100 text-center text-xl'
                    : 'bg-gray-900 focus:bg-gray-900 duration-300 hover:bg-gray-800 focus:outline-none text-gray-300 text-center text-lg',
                ]"
                @mousedown="startprogressbutton(togglestreaming)"
                @mouseleave="stopprogressbutton"
                @mouseup="stopprogressbutton"
                @touchstart="startprogressbutton(togglestreaming)"
                @touchend="stopprogressbutton"
                @touchcancel="stopprogressbutton"
                v-if="connected"
              >
                {{ streamingstate }}
                <span v-if="streamingstate == 'Streaming'">{{
                  streamingtime
                }}</span>
              </button>
              <button
                :class="[
                  recordingstate == 'Recording'
                    ? 'bg-gray-600 focus:bg-gray-600 duration-300 focus:outline-none text-gray-100 text-center text-xl'
                    : 'bg-gray-900 focus:bg-gray-900 duration-300 hover:bg-gray-800 focus:outline-none text-gray-300 text-center text-lg',
                ]"
                @mousedown="startprogressbutton(togglerecording)"
                @mouseleave="stopprogressbutton"
                @mouseup="stopprogressbutton"
                @touchstart="startprogressbutton(togglerecording)"
                @touchend="stopprogressbutton"
                @touchcancel="stopprogressbutton"
                v-if="connected"
              >
                {{ recordingstate }}
              </button>
            </div>
          </div>

          <div class="grid grid-cols-1">
            <div class="grid grid-cols-1 justify-items-center bg-black">
              <img
                class="self-center"
                :src="realtimescenedata"
                alt="Current scene in real time"
              />
            </div>
          </div>
        </div>
      </div>
    </div>

    <div
      class="sticky bottom-0 bg-gray-800 text-gray-300 footer flex items-center justify-between"
    >
      <div class="ml-3" v-if="!connected">©</div>
      <div class="mr-3" v-if="!connected">
        Rights reserved to <strong>Victor Machado</strong> and the other
        creators.
      </div>

      <div class="ml-3" v-if="connected">
        © Rights reserved to <strong>Victor Machado</strong> and the other
        creators.
      </div>
      <div class="mr-3" v-if="connected">
        {{ skippedframes }} SF, {{ fps }} fps, CPU: {{ cpu }}%
      </div>
    </div>
  </div>
</template>

<script>
// ########################################################
// ########################################################
// Rights reserved to Victor Machado and the other creators
// ########################################################
// ########################################################

import OBSWebSocket from "obs-websocket-js";
const obs = new OBSWebSocket();

const documentelement = document.documentElement;
const applyfullscreen =
  documentelement.requestFullscreen ||
  documentelement.mozRequestFullScreen ||
  documentelement.webkitRequestFullScreen ||
  documentelement.msRequestFullscreen;
const leavefullscreen =
  document.exitFullscreen ||
  document.mozCancelFullScreen ||
  document.webkitCancelFullScreen ||
  document.msExitFullscreen;

export default {
  data() {
    return {
      host: "127.0.0.1:4444",
      password: "",

      error: false,
      errormessage: "",

      connecting: false,
      connected: false,

      currentscene: "",
      scenes: [],

      realtimescenedata: "",

      streamingstate: "Not streaming",
      streamingtime: "",
      recordingstate: "Not recording",

      skippedframes: 0,
      fps: 0,
      cpu: 0,

      progressbutton: {
        pressed: false,
        progress: 0,
      },
    };
  },

  mounted() {
    // Checks for the connect argument in the hash and then calls the connect function automatically
    const uri = this.hashbang();
    if ("$connect" in uri) {
      this.autoconnect(uri);
    }

    // Call at ## event
    obs.on("SwitchScenes", async (data) => {
      this.currentscene = data.sceneName;
    });

    // Call at ## event
    obs.on("AuthenticationSuccess", async () => {
      // Update status on connect
      obs.send("GetStreamingStatus").then((data) => {
        if (data.streaming) this.streamingstate = "Streaming";
        else this.streamingstate = "Not streaming";

        if (data.recording) this.recordingstate = "Recording";
        else this.recordingstate = "Not recording";
      });

      // Update preview
      await this.updaterealtimescene();

      // Recieve data
      await obs.send("SetHeartbeat", {
        enable: true,
      });
    });

    // Call at ## event
    obs.on("RecordingStarting", async () => {
      this.recordingstate = "Recording is starting...";
    });

    // Call at ## event
    obs.on("RecordingStarted", async () => {
      this.recordingstate = "Recording";
    });

    // Call at ## event
    obs.on("RecordingStopping", async () => {
      this.recordingstate = "Recording is stopping...";
    });

    // Call at ## event
    obs.on("RecordingStopped", async () => {
      this.recordingstate = "Not recording";
    });

    // Call at ## event
    obs.on("RecordingPaused", async () => {
      this.recordingstate = "Recording paused";
    });

    // Call at ## event
    obs.on("RecordingResumed", async () => {
      this.recordingstate = "Recording";
    });

    // Call at ## event
    obs.on("StreamStarting", async () => {
      this.streamingstate = "Streaming is starting...";
    });

    // Call at ## event
    obs.on("StreamStarted", async () => {
      this.streamingstate = "Streaming";
    });

    // Call at ## event
    obs.on("StreamStopping", async () => {
      this.streamingstate = "Streaming is stopping...";
    });

    // Call at ## event
    obs.on("StreamStopped", async () => {
      this.streamingstate = "Not streaming";
    });

    // Call at ## event
    obs.on("StreamPaused", async () => {
      this.streamingstate = "Streaming paused";
    });

    // Call at ## event
    obs.on("StreamResumed", async () => {
      this.streamingstate = "Streaming";
    });

    // Call at ## event
    obs.on("StreamStatus", async (data) => {
      this.time(data["total-stream-time"]);
    });

    // Call at ## event
    obs.on("Heartbeat", (data) => {
      this.skippedframes = Math.round(data.stats["output-skipped-frames"]);
      this.fps = Math.round(data.stats.fps);
      this.cpu = Math.round(data.stats["cpu-usage"]);
    });

    obs.on("ConnectionClosed", async () => {
      // Remove the connection hash
      history.replaceState({}, document.title, ".");

      this.connected = false;
    });

    // Call at ## event
    obs.on("Exiting", async () => {
      // Remove the connection hash
      history.replaceState({}, document.title, ".");

      this.connected = false;
    });
  },

  computed: {
    httpversion() {
      // HTTP version
      return location.href.replace("https://", "http://");
    },

    httpsverify() {
      // Verify the page is using SSL
      return location.protocol == "https:";
    },
  },

  methods: {
    async connect() {
      // Reset the variables before
      this.error = false;
      this.connecting = true;
      this.connected = false;

      // Try a connection
      try {
        await obs
          .connect({
            address: this.host,
            password: this.password,
            secure: location.protocol == "https:",
          })
          .then(() => {
            // Input connection hash
            document.location.hash =
              "$connect&host=" + this.host + "&password=" + this.password;

            this.error = false;
            this.connecting = false;
            this.connected = true;
            return obs.send("GetSceneList");
          })
          .then((data) => {
            this.currentscene = data["current-scene"];
            this.scenes = data.scenes;
          });
      } catch (error) {
        // Something went wrong
        this.error = true;
        this.errormessage = error.description;
        this.connecting = false;
      }
    },

    async disconnect() {
      // Close all connection
      await obs.disconnect();
      this.connected = false;
    },

    async setscene(scene) {
      // Send set current scene request to obs and await this
      await obs.send("SetCurrentScene", {
        "scene-name": scene,
      });
    },

    async updaterealtimescene() {
      // Send get actual scene preview request to obs
      if (this.connected) {
        let data = await obs.send("TakeSourceScreenshot", {
          sourceName: this.currentscene,
          embedPictureFormat: "png",
          height: 360,
          width: 640,
          compressionQuality: 50,
        });

        if (data.status == "ok") {
          this.realtimescenedata = await data.img;
        }
      }

      setTimeout(this.updaterealtimescene, 2000);
    },

    async togglestreaming() {
      // Send request to change the current state of the stream
      await obs.sendCallback("StartStopStreaming", async () => {});
    },

    async togglerecording() {
      // Send request to change the current state of the record
      await obs.sendCallback("StartStopRecording", async () => {});
    },

    async autoconnect(urivars) {
      // Reset the variables before
      this.error = false;
      this.connecting = true;
      this.connected = false;

      // Try a connection
      try {
        // The hash arguments will be used here
        await obs
          .connect({
            address: urivars.host,
            password: urivars.password,
            secure: location.protocol == "https:",
          })
          .then(() => {
            // Okay the hash connection was made, now define the local variables as the hash variables
            // Input connection hash
            document.location.hash =
              "$connect&host=" + urivars.host + "&password=" + urivars.password;

            this.error = false;
            this.connecting = false;
            this.connected = true;
            return obs.send("GetSceneList");
          })
          .then((data) => {
            this.currentscene = data["current-scene"];
            this.scenes = data.scenes;
          });
      } catch (error) {
        // Something went wrong
        this.error = true;
        this.errormessage = error.description;
        this.connecting = false;
      }
    },

    time(toconvert) {
      // Convert normal int to time format
      var date = new Date(0);
      date.setSeconds(toconvert);
      this.streamingtime = date.toISOString().substr(11, 8);
    },

    hashbang() {
      // Get all the hash arguments
      const vars = {};
      const hashes = location.hash
        .slice(location.hash.indexOf("#!") + 2)
        .split("&");

      for (let i = hashes.length; i--; ) {
        const hash = hashes[i].split("=");
        vars[hash[0]] = hash.length > 1 ? hash[1] : null;
      }

      return vars;
    },

    fullscreen() {
      // Full screen
      const isfullscreen =
        document.fullscreen ||
        document.mozFullScreen ||
        document.webkitIsFullScreen ||
        document.msFullscreenElement;

      if (isfullscreen) {
        leavefullscreen.apply(document);
      } else {
        applyfullscreen.apply(documentelement);
      }
    },

    startprogressbutton(callback) {
      // Long click event start
      if (!this.progressbutton.pressed)
        this.progressbutton.pressed = setInterval(() => {
          this.progressbutton.progress++;
          if (this.progressbutton.progress == 200) {
            callback();
            clearInterval(this.progressbutton.pressed);
            this.progressbutton.pressed = false;
            this.progressbutton.progress = 0;
          }
        }, 1);
    },

    stopprogressbutton() {
      // Long click event stop
      clearInterval(this.progressbutton.pressed);
      this.progressbutton.pressed = false;
      this.progressbutton.progress = 0;
    },
  },
};
</script>

<style scoped>
.header {
  height: 40px;
}

.connected {
  height: calc(100vh - 80px);
}

.footer {
  height: 40px;
}
</style>